import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';


class App extends React.Component{
  constructor(props){
  super(props)
    this.state = {
      timerType: 'Session',
      runtime: 'stopped',
      reset: 'RESET',
      timer: 1500,
      breakL: 5,
      sessionL: 25,
      intervalID: '',
    }
  }

  breakDec(){ if(this.state.breakL > 1){ this.setState({ breakL: this.state.breakL-1 }) }
  }
  breakInc(){  if(this.state.breakL < 60){ this.setState({ breakL: this.state.breakL+1 }) }
  }
  sessionDec(){ if (this.state.sessionL > 1){ 
    this.setState({ sessionL: this.state.sessionL-1, timer: this.state.timer-60 })
    }
  }
  sessionInc(){ if (this.state.sessionL < 60){
    this.setState({ sessionL: this.state.sessionL+1, timer: this.state.timer+60 })
    }
  }
  reset(){
    this.setState({
      runtime: 'stopped',
      timerType: 'Session',
      timer: 1500,
      breakL: 5,
      sessionL: 25,
      intervalID: ''
    })
    this.audioBeep.pause();
    this.audioBeep.currentTime = 0;
    clearInterval(this.state.intervalID)
  }
  clockify() {
    let minutes = Math.floor(this.state.timer / 60);
    let seconds = this.state.timer - minutes * 60;
    seconds = seconds < 10 ? '0' + seconds : seconds;
    minutes = minutes < 10 ? '0' + minutes : minutes;
    return minutes + ':' + seconds;
  }
  start_stop() {
    if (this.state.runtime === 'stopped') {
      this.beginCountDown();
      this.setState({ runtime: 'running' });
    } else {
      this.setState({ runtime: 'stopped' });
      if (this.state.intervalID) {
        clearInterval(this.state.intervalID)
      }
    }
  }
  beginCountDown() { 
       this.setState({ 
      intervalID: setInterval(() => { 
        this.decrementTimer() }, 1000) })
  }
  decrementTimer() {
    if(this.state.timer>=0){ this.setState({ timer: this.state.timer-1 }) }
    
    if(this.state.timerType=='Session' && this.state.timer<0){
      this.setState({
        timer: this.state.breakL * 60,
        timerType: 'Break'
      })
    }
    if( this.state.timerType=='Break' && this.state.timer<0){
      this.setState({
        timer: this.state.sessionL*60,
        timerType: 'Session'
      })
    }
    if (this.state.timer===0){ 
         this.audioBeep.play()
      } 
  }
  render(){ 
    return (
      <div>
        <div id='main-title'>25 + 5 Clock</div>
        <div id='break-label'>Break Length</div>
        <button id='break-decrement' onClick={this.breakDec.bind(this)}>&#x21e9;</button>
         <span id='break-length'>{this.state.breakL}</span>
        <button id='break-increment' onClick={this.breakInc.bind(this)}> &#8679;</button>
        <div id='session-label'>Session Length</div>
        <button id='session-decrement' onClick={this.sessionDec.bind(this)}>&#x21e9;</button>
        <span id='session-length'>{this.state.sessionL}</span>
        <button id='session-increment' onClick={this.sessionInc.bind(this)}>&#8679;</button>
        <div id='timer-container'>
          <div id="timer-label">{ this.state.timerType }</div>
          <div id='time-left'> { this.clockify() }</div>
        </div>
        <button id='start_stop' onClick={ this.start_stop.bind(this) }>&#10151; &#10074;&#10074;</button>
        <button id='reset' onClick={ this.reset.bind(this) }>RESET</button>
        <audio id="beep" preload="auto" ref={audio=>{ this.audioBeep = audio }}
        src="https://raw.githubusercontent.com/freeCodeCamp/cdn/master/build/testable-projects-fcc/audio/BeepSound.wav" />
      </div>
    )
  }
}
ReactDOM.render(<App />, document.getElementById('root'))
